<template>
  <v-stepper v-model="e1">
    <v-stepper-header>
      <v-stepper-step :complete="e1 > 1" step="1">Voc  um escotista?</v-stepper-step>
      <v-divider></v-divider>
      <v-stepper-step :complete="e1 > 2" step="2">Acesso ao mAPPa</v-stepper-step>
      <v-divider></v-divider>
      <v-stepper-step step="3">Fim</v-stepper-step>
    </v-stepper-header>

    <v-stepper-items>
      <v-stepper-content step="1">
        <v-card class="mb-12" color="grey lighten-1" height="200px">
          <v-card-title>Recurso exclusivo para escotistas registrados na Unio dos Escoteiros do Brasil</v-card-title>

          <v-card-text>
            Nosso sistema permite que voc tenha um acesso mais amigvel aos dados da sua seo, que so registrados pelo aplicativo mAPPa Adulto.
            <br />Para tanto, ser necessrio que voc informe suas credenciais utilizadas no mAPPa Adulto.
            <br />
            <strong>Garantimos que sua senha no ser armazenada,</strong> pois o sistema de autenticao  baseado em chaves temporrias. Sua senha ser utilizada apenas para o acesso e descartada em seguida.
            <br />Para mais informaes, consulte nosso Termo de Uso e a Poltica de Privacidade (links no rodap do site).
          </v-card-text>
        </v-card>
        <v-btn color="primary" @click="e1=2">Continuar</v-btn>

        <v-btn text @click="naoQueroAgora">No quero fazer isso agora</v-btn>
      </v-stepper-content>

      <v-stepper-content step="2">
        <v-card class="mb-12" color="grey lighten-1" height="200px">
          <v-card-title>Informe suas credenciais do mAPPa</v-card-title>
          <v-card-text>
            <v-form v-model="mappaValid">
              <v-container>
                <v-row>
                  <v-col cols="12" md="4">
                    <v-text-field
                      v-model="mappa.username"
                      label="Usurio mAPPa"
                      :rules="userRules"
                      required
                    />
                  </v-col>
                  <v-col cols="12" md="4">
                    <v-text-field
                      v-model="mappa.password"
                      label="Senha do aplicativo mAPPa"
                      :rules="passRules"
                      required
                      type="password"
                    />
                  </v-col>
                </v-row>
              </v-container>
            </v-form>
          </v-card-text>
        </v-card>
        <v-alert prominent type="error" v-if="erroLogin" dismissible close-text="Fechar">
          <v-row align="center">
            <v-col class="grow">{{erroLogin}}</v-col>
          </v-row>
        </v-alert>

        <v-btn color="primary" :disabled="!mappaValid" @click="validarMAPPA">Continuar</v-btn>

        <v-btn text @click="naoQueroAgora">No quero fazer isso agora</v-btn>
      </v-stepper-content>

      <v-stepper-content step="3">
        <v-card class="mb-12" color="grey lighten-1" height="200px">
          <v-form readonly>
            <v-container>
              <v-row>
                <v-col cols="12" md="4">
                  <v-text-field v-model="user.nome" label="Nome" />
                </v-col>

                <v-col cols="12" md="4">
                  <v-text-field v-model="user.grupo" label="Grupo Escoteiro" />
                </v-col>
                <v-col cols="12" md="4">
                  <v-text-field v-model="secoes" label="Seo(es)" />
                </v-col>
              </v-row>
            </v-container>
          </v-form>
        </v-card>

        <v-btn color="secondary" :disabled="!mappaLoginOk" @click="obterSecoes">Obter Sees</v-btn>
        <v-btn color="primary" @click="finalizar">Finalizar</v-btn>
      </v-stepper-content>
    </v-stepper-items>
  </v-stepper>
</template>

<script>
import store from "../../store";
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      e1: 1,
      mappa: { username: null, password: null },
      mappaValid: false,
      userRules: [(v) => !!v || "Usurio mAPPa  requerido"],
      passRules: [(v) => !!v || "Senha mAPPA  requerida"],
      erroLogin: "",
      mappaLoginOk: false,
    };
  },
  computed: {
    ...mapGetters("mappa", ["getMappa", "getSecoes"]),
    user() {
      return {
        nome: this.getMappa.nome_completo,
        grupo:
          this.getMappa.cod_grupo +
          "/" +
          this.getMappa.cod_regiao +
          " " +
          this.getMappa.nom_grupo,
        modalidade: ["Alcatia", "Tropa Escoteira", ""],
      };
    },
    secoes() {
      return this.getSecoes.map((x) => x.tipo + ": " + x.nome).join(", ");
    },
  },
  methods: {
    validarMAPPA() {
      this.erroLogin = "";
      window.axios
        .post("/api/mappa/login", {
          username: this.mappa.username,
          password: this.mappa.password,
        })
        .then((response) => {
          console.log("MAPPA LOGIN", response);
          store.dispatch("mappa/setMappa", response.data);
          this.mappaLoginOk = true;

          this.e1 = 3;
        })
        .catch((error) => {
          console.error("MAPPA LOGIN", error);
          this.erroLogin = error.data;
        });
    },
    naoQueroAgora() {},
    finalizar() {},
    obterSecoes() {
      let data = {
        user_id: this.getMappa.user_id,
        authentication: this.getMappa.authorization,
        username: this.mappa.username,
        auth_valid_until: this.getMappa.auth_valid_until,
      };
      window.axios
        .post("/api/mappa/secoes", data)
        .then((resp_sec) => {
          console.log("MAPPA SECOES", resp_sec);
          store.dispatch("mappa/setSecoes", resp_sec.data);
        })
        .catch((error) => {
          console.error("MAPPA SECOES", error);
        });
    },
  },
};
</script>

