export const isValidEmail = (email) => (/^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(email))

export const isValidUserName = (userName) => (/[a-zA-Z0-9.-]{5,20}/.test(userName))
export const userNameRules = "De 5 a 20 caracteres alfanumricos, ponto e hfen"

import { ENVIRONMENT } from './consts'

const develop = ENVIRONMENT == 'DEVELOPMENT'

export const passwordRules = [
    (v) => v.length >= 5 || 'mnimo de 5 caracteres',
]

if (!develop) {
    passwordRules.push((v) => /[A-Z]/.test(v) || 'letras maisculas')
    passwordRules.push((v) => /[a-z]/.test(v) || 'letras minsculas')
    passwordRules.push((v) => /\d/.test(v) || "nmeros")
    passwordRules.push((v) => /[!@#$%^&*()_+\-=[\]{};':"\\|,.<>/?]/.test(v) || 'smbolos')
}

export const passwordNeeds = (password) => {
    let msgRequer = [];
    if (!develop) {
        if (!/\d/.test(password)) {
            msgRequer.push("nmero(s)")
        }
        if (!/[A-Z]/.test(password)) {
            msgRequer.push("letra(s) maiscula(s)")
        }
        if (!/[a-z]/.test(password)) {
            msgRequer.push("letra(s) minscula(s)")
        }
        if (!/[!@#$%^&*()_+\-=[\]{};':"\\|,.<>/?]/.test(password)) {
            msgRequer.push("smbolo(s)")
        }
    }
    if (password.length < 5) {
        msgRequer.push("mnimo de 5 caracteres")
    }
    return msgRequer
}

export const isValidPassword = (password) => passwordNeeds(password).length == 0